import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { EVChargingStationFranchiseCrudComponent } from './EV-charging-station-franchise-crud/EV-charging-station-franchise-crud.component';
import { TABS_DEMO_ROUTES } from './EV-charging-station-franchise-crud/routes';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : 'crud',component : EVChargingStationFranchiseCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    },
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class EVChargingStationFranchiseRoutingModule{}