import { ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActionDialogComponent } from '../action-dialog/action-dialog.component';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { MatTableFilter } from 'mat-table-filter';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { Observable } from 'rxjs';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import * as XLSX from 'xlsx';
import { ViewDialogComponent } from '../view-dialog/view-dialog.component';
import { jsPDF } from 'jspdf';
import 'jspdf-autotable';
import { ClientService } from 'app/shared/services/client/client.service';
import { ChargingStationService } from 'app/shared/services/charging-station/charging-station.service';
import { CpoService } from 'app/shared/services/cpo/cpo.service';
import { map, startWith } from 'rxjs/operators';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { EVDealersService } from 'app/shared/services/ev-space/ev-dealers/EV-dealers.service';

export class EVDealers {
  id: string;
  company_name: string;
  deal_type_id: number;
  deal_type:string;
  website: string;
  email: string;
  cp_name: string;
  mobile: number;
  address1: string;
  address2: string;
  PIN: number;
  landmark: string;
  city_name: string;
  state_id: number;
  state_name: string;
  country_id: number;
  country_name: string;
  remarks: string;
  status: string;
  created_date: string;
  createdby: number;
  modify_date: string;
  modifyby: number;
  statusString: string;
}

@Component({
  selector: 'app-moderate-list',
  templateUrl: './moderate-list.component.html',
  styleUrls: ['./moderate-list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations
})

export class ModerateListComponent implements OnInit {

  filterEntity: EVDealers;
  filterType: MatTableFilter;
  displayedColumns: string[] = ['deal_type_id', 'company_name', 'website', 'email', 'city_name', 'state_name', 'status', 'action'];
  head = [['Deal Type', 'Company Name', 'Website', 'Email', 'City', 'State', 'Status']]
  dataSource: MatTableDataSource<EVDealers>;

  localStorage: any;
  allChargers: any;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
  dialogRef: any;
  dealIdSearch = false;
  compNameSearch = false;
  websiteSearch = false;
  emailSearch = false;
  citySearch = false;
  stateSearch = false;
  statusSearch = false;
  exportedData: any[];
  form: FormGroup;
  evModDealers: any;
  
  constructor(private evDealersService: EVDealersService, public dialog: MatDialog,private cpoService: CpoService,private fb: FormBuilder, 
    private chargingStationService: ChargingStationService,private clientService:ClientService,private snackBar: MatSnackBar, 
    private cdr: ChangeDetectorRef) {
      
  }

  ngOnInit() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    this.filterEntity = new EVDealers();
    this.filterType = MatTableFilter.ANYWHERE;
    this.getAllModEvDealers();
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getAllModEvDealers() {
    this.evDealersService.getModerateEvDealerListByKeys().subscribe(
      (resp: any) => {
        if (resp.status == true) {
          this.evModDealers = resp.data;
          for (var i = 0; i < this.evModDealers.length; i++) {
            if (this.evModDealers[i].status == 'N') {
              this.evModDealers[i].statusString = 'InActive';
            } else {
              this.evModDealers[i].statusString = 'Moderate';
            }
          }
          this.dataSource = new MatTableDataSource(this.evModDealers);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
          this.cdr.detectChanges();
        } else{
          this.snackBar.open(MessageConstant.NoRecordFoundMessage, 'Dismiss',{duration : 3000 })
          return;
        }

      }, (err) => {
      }
    )
  }
  delete(id) {
    this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
      disableClose: true
    });
    this.confirmDialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.evDealersService.deleteEvDealer(id, this.localStorage.userId).subscribe(
          (resp: any) => {
            if(resp.status == true){
              this.getAllModEvDealers();
              this.cdr.detectChanges();
              this.snackBar.open(MessageConstant.deleteMessage, 'Dismiss', { duration: 2000 });
            }else{
              this.snackBar.open(resp.message, 'Dismiss', { duration: 2000 });
            }
            
          }, (err) => {
            this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 2000 });
          }
        )
      }
      this.confirmDialogRef = null;
    });

  }

  openDialog(action, data): void {
    let params = {
      action: action,
      data: data
    }
    const dialogRef = this.dialog.open(ActionDialogComponent, {
      width: '1200px',
      data: params,
      autoFocus: false,
      disableClose: true,
      panelClass: 'app-action-dialog',
    });
    dialogRef.afterClosed().subscribe(result => {
      this.getAllModEvDealers();
      this.cdr.detectChanges();
    });
  }

  viewDialog(action,data,buttonAction): void {
    let params = {
      action: action,
      data: data,
      buttonAction:buttonAction
    }
    const dialogRef = this.dialog.open(ViewDialogComponent, {
      width: '1200px',
      data: params,
      panelClass: 'app-action-dialog',
      autoFocus: false,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
      this.cdr.detectChanges();
      this.getAllModEvDealers();
      
    });
  }

  changeVisibility(visibleOption, ev) {
    ev.stopPropagation();
    if (visibleOption == 'dealIdSearch') {
      this.dealIdSearch = !this.dealIdSearch;
      this.filterEntity.deal_type = "";
    }
    if (visibleOption == 'compNameSearch') {
      this.compNameSearch = !this.compNameSearch;
      this.filterEntity.company_name = "";
    }
    if (visibleOption == 'websiteSearch') {
      this.websiteSearch = !this.websiteSearch;
      this.filterEntity.website = "";
    }
    if (visibleOption == 'emailSearch') {
      this.emailSearch = !this.emailSearch;
      this.filterEntity.email = "";
    }
    if (visibleOption == 'citySearch') {
      this.citySearch = !this.citySearch;
      this.filterEntity.city_name = "";
    }
    if (visibleOption == 'stateSearch') {
      this.stateSearch = !this.stateSearch;
      this.filterEntity.state_name = "";
    }
    if (visibleOption == 'statusSearch') {
      this.statusSearch = !this.statusSearch;
      this.filterEntity.statusString = "";
    }
  }

  tableExport() {
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var evDealers = this.dataSource.filteredData[i];
      var data = {
        'Deal Type': evDealers.deal_type_id, 'Company Name': evDealers.company_name, 'Website': evDealers.website,
        'Email': evDealers.email, 'City': evDealers.city_name, 'State': evDealers.state_name,
        'Status': evDealers.statusString
      };
      this.exportedData.push(data);
    }
    const workSheet = XLSX.utils.json_to_sheet(this.exportedData);
    const workBook: XLSX.WorkBook = XLSX.utils.book_new();

    XLSX.utils.book_append_sheet(workBook, workSheet, 'Ev Charging Station Franchise ModerateList');
    XLSX.writeFile(workBook, 'Ev Charging Station Franchise ModerateList.xlsx');
  }

  pdfExport() {
    var doc = new jsPDF({ // set needed dimensions for any elemen

    });
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var evDealers = this.dataSource.filteredData[i];

      var data = [evDealers.deal_type_id, evDealers.company_name, evDealers.website, evDealers.email, evDealers.city_name,
      evDealers.state_name, evDealers.statusString];

      this.exportedData.push(data);
    }
    var img = new Image();
    var imgRight = new Image();
    var src = 'assets/images/logos/logo_sparkev_main.png';
    var srcSparkEV = 'assets/images/logos/logo_SparkEV_black.png';
    imgRight.src = srcSparkEV;
    var createdDate = new Date().toLocaleString(('en-GB'));
    img.src = src;
    doc.setFontSize(11);
    doc.setTextColor(100);
    doc.setFontSize(18);

    (doc as any).autoTable({
      head: this.head,
      body: this.exportedData,
      theme: 'grid',
      headStyles: { fontStyle: 'majalla', fillColor: [11, 148, 68] },
      styles: {
        cellPadding: 1,
        fontSize: 8,

      },

      margin: { top: 22 },
      didDrawCell: data => {
      },
      didDrawPage: data => {
        doc.addImage(img , 15, 7, 15, 6);
        doc.addImage(imgRight, 170, 7, 25, 6);

        doc.text('Ev Charging Station Franchise ModerateList Report', doc.internal.pageSize.getWidth() / 2, 11, { align: 'center' });

        doc.setTextColor(211, 211, 211);
        doc.setFontSize(60);
        doc.setGState(doc.GState({ opacity: 0.6 }));
        doc.text('Confidential/Not for Public use', 25, 285, null, 60,);
        doc.setGState(doc.GState({ opacity: 1 }));
        doc.setTextColor(100);
        doc.setFontSize(9);
        doc.text('Date:' + createdDate, 15, 18);

        doc.text('Generated By:' + this.localStorage.f_Name, 195, 18, { align: 'right' });
        doc = this.addWaterMark(doc);
      }
    })

    // below line for Open PDF document in new tab
    doc.output('dataurlnewwindow');
    // below line for Download PDF document  
    doc.save('Ev Charging Station Franchise ModerateList report.pdf');

  }
  addWaterMark(doc) {
    var newpage = doc.getNumberOfPages();
    const i = doc.internal.getNumberOfPages();
    doc.setTextColor(100);
    doc.setFontSize(10);
    //Print Page 1 of 4 for example

    doc.text('Page ' + String(i) + ' of ' + String(newpage), 210 - 20, 297 - 10, null, null, "right");
    return doc;
  }

 

}

