import { Routes } from '@angular/router';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';

import { CreateComponent } from '../create/create.component';
import { ModerateListComponent } from '../moderate-list/moderate-list.component';
import { ListComponent } from '../list/list.component';

export const TABS_DEMO_ROUTES: Routes = [
  {
     path : 'create', component : CreateComponent,canActivate:[AuthGuard] 
  },
  { 
    path : 'listFranchise', component : ListComponent , canActivate:[AuthGuard] 
  },
  { 
    path : 'listModerate', component : ModerateListComponent , canActivate:[AuthGuard] 
  },
  { 
    path: '', redirectTo: 'listFranchise', pathMatch: 'full' 
  },
];
